.news{
  @include flex;
  margin: -15px;

  &__col{
    width: 20%;
    padding: 15px;
    flex-grow: 1;
  }
  &__item{
    height: 100%;
    position: relative;
    display: block;

    &:hover{
      .news__img img{
        transform: scale(1.1);
      }
    }
  }
  &__img{
    display: block;
    position: relative;
    overflow: hidden;
    margin-bottom: 20px;

    &:before{
      @include pseudoHeight(85%);
    }
    img{
      @include img-cover;
      transition: $transition-decor;
    }
  }
  &__title{
    margin-bottom: 48px;
  }
  &__link{
    display: inline-block;
    vertical-align: top;
    position: static;
    color: $base-color;
    border-bottom: 2px solid $red;
    font-size: .78em;
    font-weight: 500;

    &:before{
      @include pseudo();
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
    }

    &:hover{
      border-color: $base-color;
    }
  }

  @include respond-below(lg){
    margin: -20px -38px;

    &__col{
      width: calc(100% / 3);
      padding: 20px 38px;
      flex-grow: unset;
    }

    &__slider{
      overflow: visible;

      .swiper-slide{
        width: 202px;
      }
    }
  }
  @include respond-below(sm){
    margin: -14px;

    &__title{
      margin-bottom: 28px;
      font-style: italic;
    }
    &__link{
      font-style: italic;
    }
    &__col{
      width: 100%;
      padding: 14px;
      display: none;

      &:nth-child(1),&:nth-child(2),&:nth-child(3){
        display: block;
      }

      &.active{
        display: block;
      }
    }
  }
}

.section--details{
  background: $red;
  color: #fff;
  padding: 176px 0 60px;
  z-index: 1;

  &__row{
    @include flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: -55px;
  }
  &__content{
    padding: 55px;
    width: 50%;
  }
  &__slider{
    width: calc(50% + 100px);
    margin: -268px -100px 0 0;
    padding: 55px;
  }

  @include respond-below(lg){
    padding: 36px 0 40px;

    &__row{
      display: block;
      margin: -23px;
    }
    &__content{
      padding: 23px;
      width: 100%;
    }
    &__slider{
      width: 100%;
      margin: 0;
      padding: 23px;
    }
  }
  @include respond-below(sm){
    padding: 46px 0 0;

    &__row{
      margin: 0;
    }
    &__content{
      padding: 0;
    }
    &__slider{
      display: none;
    }
    .form-block .gform_wrapper .gform_footer {
      margin-left: -34px;
      margin-right: -34px;
      width: calc(100% + 68px);
    }
  }
}

.details-slider{
  @include respond-above(lg){
    .swiper-pagination{
      display: none !important;
    }
    .swiper-wrapper{
      flex-wrap: wrap;
      transform: none !important;

      .swiper-slide{
        width: 100% !important;

        & + .swiper-slide{
          margin-top: 34px;
        }
      }
    }
  }

  &__item{
    display: block;
    position: relative;
    overflow: hidden;

    &:before{
      @include pseudoHeight(56.25%);
    }
    img{
      @include img-cover;
    }
  }

  @include respond-below(lg){
    padding-bottom: 40px;

    .swiper-pagination{
      bottom: 0;
    }
  }
}

.section-values{
  &__row{
    @include flex;
    align-items: center;
    justify-content: space-between;
    margin: -55px;
  }
  &__photo{
    width: calc(50% + 100px);
    margin-left: -105px;
    padding: 55px;

    &-item{
      display: block;
      position: relative;
      overflow: hidden;

      &:before{
        @include pseudoHeight(119.2%)
      }
      img{
        @include img-cover;
      }
    }
  }
  &__content{
    width: 50%;
    padding: 55px;
  }

  @include respond-below(lx){
    &__photo{
      width: calc(50% + 30px);
      margin-left: -35px;
    }
  }
  @include respond-below(lg){
    &__row{
      align-items: unset;
      margin: -14px;
    }
    &__photo{
      width: 50%;
      margin-left: 0;
      padding: 14px;

      &-item{
        height: 100%;
      }
    }
    &__content{
      width: 50%;
      padding: 14px;
    }
  }
  @include respond-below(lg){
    &__photo{
      width: calc(100% + 68px);
      margin: 0 -34px;

      &-item{
        height: auto;
      }
    }
    &__content{
      width: 100%;
    }
  }
}

.section-key{
  &__row{
    @include flex;
    align-items: center;
  }
  &__content{
    width: 50%;
    padding-right: 100px;

    h3{
      font-size: 1.33em;
      margin: 2em 0 .9em;
    }
    p{
      font-size: .88em;
    }
    ul{
      @include listReset;
      font-size: .88em;

      li{
        padding-left: 36px;
        position: relative;

        &:before{
          @include pseudo();
          width: 12px;
          height: 2px;
          top: .65em;
          background: $red;
          left: 0;
        }
      }
    }
  }
  &__media{
    width: calc(50% + 150px);
    margin-right: -150px;

    &-photo{
      display: block;
      position: relative;
      overflow: hidden;
      margin-bottom: 16px;

      &:before{
        @include pseudoHeight(69.5%)
      }
      img{
        @include img-cover;
      }
    }

    &-list{
      @include flex;
      margin: -7px;
      padding-right: 14px;
    }
    &-col{
      padding: 7px;
      width: calc(100% / 3);
    }
    &-item{
      height: 100%;
      background: #F5F5F5;
      padding: 66px 20px 20px;
      font-size: .88em;

      h4{
        font-size: 1em;
        font-style: italic;
        margin-bottom: 1em;
      }
      p{
        margin: 0;
      }

      img{
        display: block;
        height: 34px;
        margin-bottom: 20px;
      }
    }
  }

  @include respond-below(lx){
    &__media{
      width: calc(50% + 60px);
      margin-right: -60px;
    }
  }
  @include respond-below(lg){
    &__content{
      width: calc(51% + 38px);
      padding-right: 15px;
      min-height: 609px;
    }
    &__media{
      width: 100%;
      margin-right: 0;
      position: relative;
      padding-top: 35px;

      &-photo{
        position: absolute;
        margin-bottom: 0;
        height: 609px;
        width: 49%;
        right: -38px;
        bottom: 100%;
      }

      &-list{
        @include flex;
        margin: -7px;
        padding-right: 0;
      }
    }
  }

  @include respond-below(sm){
    &__content{
      width: 100%;
      padding-right: 0;
      min-height: unset;
    }
    &__media{
      &-photo{
        position: relative;
        height: auto;
        width: calc(100% + 68px);
        margin: 0 -34px 50px;
        right: 0;
        bottom: 0;
      }

      &-list{
        margin: -17px;
      }
      &-col{
        width: 100%;
        padding: 17px;
      }
    }
  }
}

.section-reviews{
  .reviews-slider__pagination{
    display: none;
  }
  .reviews-slider__prev,.reviews-slider__next{
    width: 95px;
    height: 146px;
    position: absolute;
    border-radius: 0;
    top: 50%;
    margin-top: -73px;
  }
  .reviews-slider__prev{
    left: 0;
    right: 0;
  }
  .reviews-slider__next{
    right: 0;
    left: auto;
  }

  @include respond-below(sm){
    .reviews-slider__pagination{
      display: block;
      bottom: 20px;
      width: 100%;
      left: 0;
    }
    .reviews-slider__prev,.reviews-slider__next{
      display: none;
    }
  }
}

.reviews-slider{
  &__item{
    font-size: 1.33em;
    font-weight: 500;
    text-align: center;
    max-width: 540px;
    margin: 0 auto;
    line-height: 1.33;

    p{
      margin: 0;
    }
  }
  &__avatar{
    display: block;
    margin: 0 auto 13px;
    border-radius: 50%;
    overflow: hidden;
    @include size(140px);

    img{
      @include size(100%);
      display: block;
      object-fit: cover;
    }
  }
  &__title{
    display: block;
    font-size: .66em;
    margin-top: 20px;
    color: $red;
  }

  @include respond-below(sm){
    &__item{
      font-size: 1em;
      font-style: italic;
    }
  }
}

.main--grid{
  position: absolute;
  top: 0;
  bottom: 0;
  pointer-events: none;
  width: 1440px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 150px;
  display: flex;

  span{
    width: 20%;
    border-right: 1px solid #F2F2F2;

    &:first-child{
      border-left: 1px solid #F2F2F2;
    }
  }

  @include respond-below(xl){
    width: 100%;
  }
  @include respond-below(lx){
    padding-left: 60px;
    padding-right: 60px;
  }
  @include respond-below(lg){
    display: none;
  }
}

.row{
  @include flex;
  margin: -15px;

  .col{
    padding: 15px;
  }
  .col-12{
    width: 100%;
  }
  .col-sm-6{
    @include respond-above(sm){
      width: 50%;
    }
  }
}

.section-summary{
  &__row{
    @include flex;
  }
  &__media{
    width: 55%;
    position: relative;
    margin-top: -30px;
    min-height: 610px;
    &-item{
      width: 990px;
      right: 30px;
      position: absolute;
      top: 0;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
      overflow: hidden;
      pointer-events: none;

      &:nth-child(1){
        z-index: 2;
      }
      &:nth-child(2){
        top: 27px;
        right: 57px;
        z-index: 1;
      }
      &:nth-child(3){
        top: 54px;
        right: 84px;
      }

      &:before{
        @include pseudoHeight(56%);
      }
      img{
        @include img-cover;
      }
    }
  }
  &__content{
    width: calc(45% + 60px);
    margin-right: -60px;
    padding-left: 60px;

    h4{
      font-size: 1em;
      margin: 1em 0 .88em;
    }

    p,ul{
      font-size: .88em;
    }

    ul{
      @include listReset;

      li{
        position: relative;
        padding-left: 30px;

        & + li{
          margin-top: 8px;
        }

        &:before{
          @include pseudo();
          border: 1px solid $red;
          border-top: 0;
          border-left: 0;
          width: .5em;
          height: 1em;
          left: 0;
          top: 0;
          transform: rotate(45deg);
        }
      }
    }
  }

  @include respond-below(lx){
    &__content{
      width: 45%;
      margin-right: 0;
    }
  }
  @include respond-below(lg){
    &__media{
      width: 100%;
      margin-top: 0;
      min-height: 555px;
      &-item{
        right: 80px;
        display: none;

        &:nth-child(1){
          display: block;
        }
      }
    }
    &__content{
      width: 100%;
      margin-right: 0;
      padding-left: 0;
      padding-top: 35px;
    }
  }
  @include respond-below(sm){
    &__row{
      flex-direction: column-reverse;
    }
    &__media{
      width: 100%;
      margin-top: 0;
      min-height: 312px;
      &-item{
        right: 0;
        width: 560px;
      }
    }
    &__content{
      padding-top: 0;
      padding-bottom: 35px;
    }
  }
}

.section-jumbotron{
  color: #fff;
  padding: 0;
  &__row{
    display: flex;
  }
  &__content{
    width: calc(55% + 60px);
    margin-left: -60px;
    padding: 82px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    
    .section__tag{
      color: #fff;
    }
  }
  &__form{
    width: calc(45% + 150px);
    margin-right: -150px;
    background: $red;
    padding: 90px 120px 0 100px;

    .form-block .gform_wrapper .gform_footer {
      margin-top: 0!important;
      margin-bottom: -158px !important;
    }
    .form-block .gform_wrapper .gform_footer .gform_button{
      height: 158px !important;
      line-height: 158px !important;
    }
    .form-block .gform_wrapper{
      margin-left: -100px;
      margin-right: -120px;
      width: calc(100% + 220px);
    }
    .form-block .gform_body {
      padding: 30px 120px 96px 100px;
    }
    .form-block-t2 .gform_wrapper .gform_fields .gfield:nth-child(3), .form-block-t2 .gform_wrapper .gform_fields .gfield:nth-child(4) {
      width: 50%!important;
    }
  }
  &__partners{
    width: 55%;
    color: $base-color;
    padding: 23px 0;
  }

  @include respond-below(lx){
    &__content{
      width: calc(55% + 60px);
      margin-left: -60px;
      padding: 60px;
    }
    &__form{
      width: calc(45% + 60px);
      margin-right: -60px;
      padding: 90px 60px 0 60px;

      .form-block .gform_wrapper{
        margin-left: -60px;
        margin-right: -60px;
        width: calc(100% + 120px);
      }
      .form-block .gform_body {
        padding: 30px 60px 96px 60px;
      }
    }
  }

  @include respond-below(lg){
    &__content{
      width: calc(50% + 38px);
      margin-left: -38px;
      padding: 98px 15px 38px 38px;
      justify-content: flex-start;

      .section__tag{
        font-style: italic;
      }
    }
    &__form{
      width: calc(50% + 38px);
      margin-right: -38px;
      padding: 98px 38px 102px 38px;

      .form-block .gform_wrapper .gform_footer {
        margin-top: 0!important;
        margin-bottom: -102px !important;
        padding-bottom: 0 !important;
      }
      .form-block .gform_wrapper .gform_footer .gform_button{
        height: 102px !important;
        line-height: 102px !important;
      }
      .form-block .gform_wrapper{
        margin-left: -38px;
        margin-right: -38px;
        width: calc(100% + 76px);
      }
      .form-block .gform_body {
        padding: 25px 38px;
      }
      .form-block .gform_wrapper .gform_fields .gfield:nth-child(1), .form-block .gform_wrapper .gform_fields .gfield:nth-child(2) {
        width: 100%!important;
      }
      .form-block-t2 .gform_wrapper .gform_fields .gfield:nth-child(3), .form-block-t2 .gform_wrapper .gform_fields .gfield:nth-child(4) {
        width: 100%!important;
      }
    }
    &__partners{
      width: 100%;
      padding: 35px 0 0;
    }
  }

  @include respond-below(sm){
    &__row{
      display: block;
    }
    &__content{
      width: calc(100% + 68px);
      margin-left: -34px;
      margin-right: -34px;
      padding: 80px 34px;
      justify-content: flex-end;
      min-height: 640px;

      h1,.h1{
        font-style: italic;
      }
    }
    &__form{
      width: calc(100% + 68px);
      margin-left: -34px;
      margin-right: -34px;
      padding: 35px 34px 102px 34px;

      h3{
        font-style: italic;
      }

      .form-block .gform_wrapper{
        margin-left: -34px;
        margin-right: -34px;
        width: calc(100% + 68px);
      }
      .form-block .gform_body {
        padding: 25px 34px;
      }
    }
  }

}

.partners-slider{

  .swiper-slide{
    width: auto;
  }

  &__item{
    height: 83px;
    display: block;

    img{
      height: 100%;
    }
  }

  @include respond-below(lg){
    overflow: visible;
  }
}

.header{
  position: relative;
  z-index: 99;
  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 90px;
  }
  &-brand{
    color: $red;
    font-size: 1.88em;
    font-weight: 500;
    display: inline-block;
    vertical-align: top;
  }
  &-nav{
    margin-right: -150px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &__pull{
      display: none;
    }
  }

  @include respond-below(lx){
    &-nav{
      margin-right: -60px;
    }
  }
  @include respond-below(lg){
    &-nav{
      margin-right: -38px;
    }
    &-brand{
      font-style: italic;
    }
  }
  @include respond-below(sm){
    &-nav{
      margin-right: 0;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      top: 100%;
      left: -34px;
      right: -34px;
      position: absolute;
      background: #000;
      padding: 34px;
      transition: $transition;
      @include hide;

      &.active{
        @include show;
      }

      &__pull{
        display: block;
        width: 90px;
        height: 90px;
        margin-right: -34px;
        background: $base-color;
        position: relative;
        padding: 0;

        span{
          background: #fff;
          height: 2px;
          position: absolute;
          width: 24px;
          left: 50%;
          margin-left: -12px;
          margin-top: -1px;
          top: 50%;
          transition: $transition;

          &:nth-child(1){
            margin-top: -9px;
          }
          &:nth-child(3){
            margin-top: 7px;
          }
        }

        &.active{
          span{
            &:nth-child(1){
              margin-top: -1px;
              transform: rotate(45deg);
            }
            &:nth-child(2){
              margin-top: -1px;
              transform: rotate(45deg);
              opacity: 0;
            }
            &:nth-child(3){
              margin-top: -1px;
              transform: rotate(-45deg);
            }
          }
        }
      }
    }
  }
}


.section__btn-news.section__btn{
  display: none;

  @include respond-below(sm){
    display: block;

    .btn{
      width: 100%;
    }
  }
}

.footer{
  background: $base-color;
  color: #fff;

  &__brand{
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;

    img{
      display: block;
      width: 100%;
    }
  }

  &__main{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-size: 16px;
    padding: 107px 0;
    position: relative;

    &-info{
      position: static;
      width: 290px;

      p{
        margin-top: 0;
      }
    }
    &-controls{
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
    }
    &-nav{
      padding-left: 50px;
      font-weight: 500;

      ul{
        @include listReset;

        li + li{
          margin-top: 28px;
        }
      }

      a{
        color: #fff;
        display: inline-block;
        vertical-align: top;

        .icon{
          display: inline-block;
          vertical-align: middle;
          margin-right: .7em;
        }

        &:hover{
          color: $red;
        }
      }
    }
  }
  &__copyright{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding: 56px 0;

    ul{
      @include listReset;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: -5px;

      li{
        margin: 5px;
      }
      a{
        text-decoration: underline;
        color: inherit;

        &:hover{
          color: $red;
        }
      }
    }
  }

  @include respond-below(lg){
    &__copyright{
      padding: 40px 0;
    }
    &__main{
      padding: 94px 0 10px;
      position: relative;

      &-info{
        width: 280px;
      }
    }
  }
  @include respond-between(sm,sd){
    &__main{
      font-size: 14px;

      &-info{
        width: 270px;
      }

      &-nav{
        padding-left: 30px;
      }
    }
  }
  @include respond-below(sm){
    &__main{
      display: block;
      &-info{
        width: 100%;
        margin-bottom: 50px;
      }
      &-controls{
        width: 100%;
        flex-wrap: wrap;
        margin: -15px;
      }
      &-nav{
        padding: 15px;
        width: auto;
        flex-grow: 1;
      }
    }
    &__copyright{
      flex-direction: column-reverse;

      &-item{
        margin-top: 10px;
      }
    }
  }
}

.form-block.form-block-subscribe{
  .gform_wrapper .gform_footer .gform_button,.gform_wrapper .gform_footer .gform_button:active, .gform_wrapper .gform_footer .gform_button:hover{
    width: 24px!important;
    height: 24px!important;
    padding: 0!important;
    color: transparent!important;
    background-size: contain !important;
    position: absolute;
    bottom: 100%;
    z-index: 2;
    margin-bottom: 20px !important;
    right: 0;
  }
  .gform_wrapper .gform_footer {
    position: relative;
    margin-top: 0 !important;
  }
  .gform_wrapper .gform_fields .gfield{
    width: 100% !important;
  }
}

.section-text{
  text-align: center;

  &__block{
    max-width: 606px;
    margin-left: auto;
    margin-right: auto;

    h2{
      margin-bottom: .8em;

      @include respond-above(sm){
        font-size: 3.1em;
      }
    }
    .btn{
      margin-top: 1.5em;
    }
  }
}

.section-thanks{
  padding: 0;
  color: #fff;

  &__row{
    background-repeat: no-repeat;
    background-position: 50%;
    -webkit-background-size: cover;
    background-size: cover;
    width: calc(100% + 205px);
    min-height: 640px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: -55px;
    padding: 80px 55px;
    align-items: flex-start;
  }
  &__txt{
    max-width: 400px;
    width: 100%;

    .section__tag{
      color: #fff;
    }
  }

  @include respond-below(lx){
    &__row{
      width: calc(100% + 120px);
      margin-left: -60px;
      padding: 80px 60px;
    }
  }
  @include respond-below(lg){
    &__row{
      width: calc(100% + 76px);
      margin-left: -38px;
      padding: 50px 38px;
      min-height: 600px;
    }
  }
  @include respond-below(sm){
    &__row{
      width: calc(100% + 68px);
      margin-left: -34px;
      padding: 50px 34px;
      min-height: 500px;
    }
    &__txt{
      max-width: 100%;
    }
  }
}

.section-partners{
  &.section{
    padding-top: 56px;

    h3{
      font-size: 16px;
    }
  }

  &__slider{
    text-align: center;

    .swiper-wrapper{
      align-items: center;
    }

    &-item{
      display: inline-block;
      vertical-align: top;
    }

    @include respond-below(lg){
      overflow: visible;
    }
  }
}