// Typical
//
//___________________________________

body{
  font-family: $base-font;
  color: $base-color;
  min-width: $body-min-width;
  position: relative;
  width: 100%;
  background: $base-bg;
  line-height: 1.55;
  font-size: 18px;

  @include respond-between(sm,sd){
    font-size: 16px;
  }
}

.wrapper{
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.header,.footer{
  flex: 0 0 auto;
  width: 100%;
  position: relative;
}
.main{
  width: 100%;
  flex: 1 0 auto;
  position: relative;
  opacity: 0;
  visibility: hidden;

  body.loaded &{
    opacity: 1;
    visibility: visible;
  }
}

.container{
  position: relative;
  z-index: 1;
  @include make-container();
  @include make-container-max-widths();

  @include respond-below(lx){
    padding-left: 60px;
    padding-right: 60px;
  }
  @include respond-below(lg){
    padding-left: 38px;
    padding-right: 38px;
  }
  @include respond-below(sm){
    padding-left: 34px;
    padding-right: 34px;
  }
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.section{
  position: relative;
  width: 100%;
  padding: 100px 0;

  p a{
    color: $red;

    &:hover{
      color: $red-active;
    }
  }

  &__title{
    margin-bottom: 50px;
  }
  &__btn{
    margin-top: 40px;
  }

  &.section-white{
    padding: 200px 0;

    & + .section-white{
      padding-top: 0;
    }
  }
  &__tag{
    display: block;
    color: $red;
    font-size: 1em;
    margin-bottom: 13px;
    font-weight: 500;
  }

  @include respond-below(lx){
    &.section-white{
      padding: 150px 0;

      & + .section-white{
        padding-top: 0;
      }
    }
  }
  @include respond-below(lg){
    padding: 45px 0;
    &.section-white{
      padding: 80px 0;

      & + .section-white{
        padding-top: 0;
      }
    }
  }
  @include respond-below(sm){
    padding: 45px 0;
    &.section-white{
      padding: 60px 0;

      & + .section-white{
        padding-top: 0;
      }
    }
    &__tag{
      font-style: italic;
      letter-spacing: -0.3px;
    }
    &__title{
      font-style: italic;
    }
  }
}

//  2. Headings

h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6{
  display: block;
  margin-top: 0;
  font-weight: 500;
  line-height: 1.12;
  letter-spacing: -0.0357em;
  margin-bottom: .5em;
}
h1,.h1{
  font-size: 3.55em;

  @include respond-below(sm){
    font-size: 2.5em;
    letter-spacing: -.07em;
    font-style: italic;
  }
}
h2,.h2{
  font-size: 2.77em;

  @include respond-below(sm){
    font-size: 2em;
    font-style: italic;
    letter-spacing: -1.78571px;
  }
}

h3,.h3{
  font-size: 1em;
}

//  3. Transition

a,.btn,.link,.form-control,.swiper-pagination-bullet,.swiper-button-next,.swiper-button-prev,h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6,.section,body,.header,.footer{
  transition: $transition;
}

//  4. FormElements/FormControl/Btn/Link

a,button,input,label{
  font-family: $base-font;
}

input,
textarea {
  font-family: inherit;
  @include input-placeholder {
    font: inherit;
    color: #fff;
  }
  &:hover{
    @include input-placeholder {
      color: #fff;
    }
  }
}

.btn{
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  padding: 0 60px;
  height: 90px;
  line-height: 90px;
  border: 0;
  cursor: pointer;
  border-radius: 0;
  position: relative;
  overflow: hidden;
  user-select: none;
  background: $red-active;
  color: #fff;

  &:hover{
    background: darken($red-active,10%);
  }

  @include respond-below(sm){
    height: 80px;
    line-height: 80px;
    font-style: italic;
    padding: 0 30px;
    width: 100%;
  }
}

.social-links{
  @include flex;
  margin: -15px;
  align-items: center;
  justify-content: center;

  &__item{
    margin: 15px;
    @include size(18px);
    color: $base-color;

    &:hover{
      color: $red;
    }

    .icon{
      display: block;
      @include size(100%);
    }
  }

  @include respond-below(lx){
    margin: -8px;
    &__item{
      margin: 8px;
      @include size(18px);
    }
  }
}

//  7. Swiper

.swiper{
  &-pagination{
    &-bullet{
      width: 12px;
      height: 12px;
      background: #010101;
      opacity: 1;
      border-radius: 50%;
      &:focus{
        outline: none;
      }
      &-active{
        background: $red-active;
        opacity: 1;
      }
    }

    .section__slider &{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;

      .swiper-pagination-bullet{
        margin: 0 5px;
      }
    }

    &.swiper-pagination-lock{
      display: none;
    }

  }

  &-container{
    .swiper-slide{
      height: auto;
    }
  }
  &-button{
    &-prev,&-next{
      margin: 0;
      color: #666666;
      background: #FAFAFA;
      font-weight: 700;
      @include size(3.4em);
      font-size: 1em;
      position: relative;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 50%;

      &:after{
        font-size: inherit;
        color: inherit;
      }

      &:hover{
        color: #666666;
        background: rgba(#FAFAFA,.3);
      }

      &:focus{
        outline: none;
      }

      @include respond-below(lg){
        background: none;
        &:hover{
          background: none;
        }
      }
    }
  }
}

.form-block{
  .gform_wrapper{
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box!important;

    *{
      box-sizing: border-box!important;
    }

    .validation_error {
      color: #fff !important;
      border: 1px solid #fff !important;
      margin: 0 0 2em !important;
      padding: 1em 1.2em !important;

      h2,h3,h4{
        padding: 0 0 0 30px;
        position: relative;
        margin: 0;
        font-size: 1.1em;
        letter-spacing: 0;

        &:before{
          content: '';
          @include size(22px);
          background-color: transparent !important;
          background-repeat: no-repeat !important;
          background-position: 50% !important;
          background-size: contain !important;
          position: absolute;
          top: 50%;
          left: 0;
          margin-top: -11px !important;
        }
      }
    }

    form{
      width: 100%;
      margin: 0;
      padding: 0;
      text-align: left!important;
    }
    .gform_body,.gform_footer{
      margin: 0;
      padding: 0;
      width: 100%;
    }
    .gform_footer{
      margin-top: 35px !important;

      .gform_button{
        display: block;
        width: 100%;
        border-radius: 0 !important;
        height: 90px !important;
        line-height: 90px !important;
        border: 0 !important;
        background: $red-active !important;
        color: #fff !important;
        transition: $transition;
        outline: none !important;
        box-shadow: none !important;
        user-select: none;
        cursor: pointer;
        font-weight: 500;
        font-size: 18px !important;
        font-family: $base-font !important;

        &:hover,&:active{
          border: 0 !important;
          outline: none !important;
          box-shadow: none !important;
          background: darken($red-active,10%) !important;
          color: #fff !important;
        }
      }
    }
    .validation_message{
      display: block !important;
      margin: .5em 0 0 !important;
      font-size: 1em !important;
      padding: 0 !important;
      color: #fff !important;
    }
    .gform_fields{
      list-style: none;
      padding: 0 !important;
      margin: -11px !important;
      text-indent: 0;
      @include flex;
      align-items: flex-start;
      justify-content: space-between;

      .gfield{
        width: 100% !important;
        padding: 15px !important;
        margin: 0 !important;

        &:nth-child(1),&:nth-child(2){
          width: 50% !important;
        }

        .gfield_label{
          display: block;
          background: none !important;
          color: #fff !important;
          font-size: 14px !important;
          padding: 0 !important;
          margin: 0 !important;
          line-height: 1 !important;
          white-space: nowrap;
        }

        .ginput_container{
          display: block;
          padding: 0 !important;
          width: 100% !important;
          margin: 0 !important;
        }

        input{
          font-family: $base-font !important;
          display: block;
          width: 100%;
          background: none !important;
          font-size: 16px !important;
          color: #fff !important;
          padding: 0 !important;
          margin: 0 !important;
          border: 0 !important;
          box-shadow: none !important;
          outline: none !important;
          border-radius: 0 !important;
          height: 51px !important;
          line-height: normal !important;
          border-bottom: 1px solid #fff !important;
          &:-moz-placeholder {
            line-height: 50px !important;
          }
          &::-moz-placeholder {
            line-height: 50px !important;
          }
          &:focus,&:active{
            border: 0 !important;
            border-bottom: 1px solid #fff !important;
            box-shadow: none !important;
            outline: none !important;
          }
        }
        textarea{
          display: block;
          width: 100%;
          background: none !important;
          font-size: 16px !important;
          color: #fff !important;
          padding: 10px 18px !important;
          margin: 0 !important;
          border: 0 !important;
          box-shadow: none !important;
          outline: none !important;
          border-radius: 0 !important;
          height: 220px !important;
          line-height: normal !important;

          &:-moz-placeholder {
            line-height: normal !important;
          }
          &::-moz-placeholder {
            line-height: normal !important;
          }

          &:focus,&:active{
            border: 0 !important;
            box-shadow: none !important;
            outline: none !important;
          }
        }

        &.gfield_error{
          .gfield_label{
            color: #fff !important;
          }
          input,textarea{
            color: #fff !important;
            border: 0 !important;
            box-shadow: none !important;
            outline: none !important;
          }
        }
      }
    }
  }

  @include respond-below(lg){
    .gform_wrapper{
      .gform_footer{
        .gform_button{
          font-style: italic;
        }
      }
    }
  }
  @include respond-below(sm){
    .gform_wrapper{
      .gform_fields{
        .gfield{
          &:nth-child(1),&:nth-child(2){
            width: 100% !important;
          }
        }
      }
    }
  }
}