.theme-green{
  .section{
    p a{
      color: $green;

      &:hover{
        color: $green-active;
      }
    }
    &__tag{
      color: $green;
    }
  }
  .btn{
    background: $green-active;

    &:hover{
      background: darken($green-active,10%);
    }
  }

  .social-links{
    &__item{
      &:hover{
        color: $green;
      }
    }
  }

  .swiper{
    &-pagination{
      &-bullet{
        &-active{
          background: $green-active;
        }
      }
    }
  }

  .form-block{
    .gform_wrapper{
      .gform_footer{
        .gform_button{
          background: $green-active !important;
          &:hover,&:active{
            background: darken($green-active,10%) !important;
          }
        }
      }
    }
  }

  .news{
    &__link{
      border-color: $green;
      &:hover{
        border-color: $base-color;
      }
    }
  }

  .section--details{
    background: $green;
  }

  .section-key{
    &__content{
      ul{
        li{
          &:before{
            background: $green;
          }
        }
      }
    }
    &__media{
      &-item{
        background: rgba(31, 151, 136, 0.1);
      }
    }
  }

  .reviews-slider{
    &__title{
      color: $green;
    }
  }
  .section-summary{
    &__content{
      ul{
        li{
          &:before{
            border-color: $green;
          }
        }
      }
    }
  }
  .section-jumbotron{
    &__form{
      background: $green;
    }
  }
}

